@import url(https://fonts.googleapis.com/css?family=Montserrat);
html, body {
  flex: 1 1;
  display: flex;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #DADADA;
  justify-content: stretch;
  align-items: stretch;
}

body {
  min-height: 100vh;
  overflow-y: scroll;
}

#root {
  flex: 1 1;
}

a {
  text-decoration:none;
  color: #008073;
}

ul {
  padding: 0 0 0 18px;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #ffffff;
  box-shadow: 0px 3px 3px rgba(0,0,0,0.2);
  padding: 25px;
  box-sizing: border-box;
  z-index: 10;
}

.site-title {
  font-size: 21px;
  font-weight: 100;
  color: #008073;
}

@media only screen and (max-width: 799px) {
  header {
    justify-content: center;
  }
}

.post-item {
  position: absolute;
  display: flex;
  box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  margin-bottom: 20px;
  box-sizing: border-box;
}

.post-item-inner {
  background-color: #ffffff;
  padding: 16px;
}

.post-item-background-image {
  flex: 1 1;
}

.post-item-background-image img {
  width: 100%;
  height: auto;
}

.post-item-background-image-small {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.post-item-background-image-small img {
  margin-top: -50px;
  width: 50%;
  height: auto;
}

.post-item-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  //top: 0;
  //margin: 160px 0 0 0;
  -webkit-transition: height 200ms;
  transition: height 200ms;
  //height: 50px;
  background-color: #ffffff;
  //background-color: #443642;
  //color: #ffffff;
  padding: 15px;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  justify-content: space-between;
}

.post-tile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.post-tile-icon {
  cursor: pointer;
  margin-left: 10px;
}

.post-tile-title {
  text-transform: uppercase;
  font-weight: 100;
  line-height: 16px;
  color: #008073;
}

.post-tile-excerpt {
  flex: 1 1;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  justify-content: center;
}

.post-tile-back {
  flex: 1 1;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  justify-content: space-between;
}

.post-tile-back-info {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.post-tile-actions {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.button {
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.icon-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 21px;
  height: 21px;
}

.icon-button img {
  width: 21px;
  height: 21px;
}

.feature-tile {
  background-color: #443642;
  color: #ffffff;
  display: flex;
  flex-flow: column;
  align-items: stretch;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
}

.post-tile-image-icon {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 15px;
  box-sizing: border-box;
}

.post-item-wide {
  position: absolute;
  display: flex;
  flex-flow: row nowrap;
  box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.post-item-background-image-small-wide {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.post-item-background-image-small-wide img {
  width: 40%;
  height: auto;
}

.post-item-info-wide {
  position: relative;
  flex: 1 1;
  background-color: #ffffff;
  padding: 15px;
  padding-left: 36px;
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  justify-content: space-between;
  line-height: 24px;
}

.arrow-left {
  position: absolute;
  top: calc(50% - 16px);
  left: -16px;
  z-index: 5;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-bottom: 16px solid transparent;

  border-right: 16px solid white;
}

.post-item-background-image-wide img {
  width: 100%;
  height: auto;
}

.App {
  flex: 1 1;
  min-height: 100vh;
  min-width: 300px;
  text-align: left;
  display: flex;
  flex-flow: column;
  font-family: 'Montserrat', sans-serif;
  line-height: 21px;
  color: #444444;
  font-size: 14px;
  margin-bottom: 20px;
}

.hiddenPreloader {
  display: none;
}

.post-grid {
  margin-top: 60px;
  position: relative;
  flex: 1 1;
}

::-webkit-scrollbar {
  display: none;
}

.loadingSpinner {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  padding: 12px 7px;
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.spinner {
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 16px;
  height: 16px;
  background-color: #008073;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}

